import { Component, ChangeDetectionStrategy } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
    selector: 'select-tags',
    changeDetection: ChangeDetectionStrategy.OnPush,
    template: `
        <p>
            By default ng-select enables virtual scroll for more 20 items. You can turn it off by setting disableVirtualScroll to true. 
            In this example we are loading 5000 items but only ~30 of them are rendered in the dom. 
            This allows to load as big data as you want.
        </p>
        <pre class="hljs" ngNonBindable><code class="lang-html"><span class="hljs-tag">&lt;<span class="hljs-name">ng-select</span> [<span class="hljs-attr">items</span>]=<span class="hljs-string">"photos"</span>
           [<span class="hljs-attr">disableVirtualScroll</span>]=<span class="hljs-string">"disableVirtualScroll"</span>
           <span class="hljs-attr">bindLabel</span>=<span class="hljs-string">"title"</span>
           <span class="hljs-attr">bindValue</span>=<span class="hljs-string">"thumbnailUrl"</span>
           <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Select photo"</span>
           [(<span class="hljs-attr">ngModel</span>)]=<span class="hljs-string">"photo"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">ng-select</span>&gt;</span></code></pre>

<ng-select [items]="photos"
           [disableVirtualScroll]="disableVirtualScroll"
           bindLabel="title"
           bindValue="thumbnailUrl"
           placeholder="Select photo"
           [(ngModel)]="photo">
</ng-select>
        <small class="form-text text-muted">5000 items with virtual scroll</small>
        <br>
        <button class="btn btn-secondary btn-sm" (click)="toggleVirtualScroll()">Toggle virtual scroll</button>
    `
})
export class VirtualScrollComponent {

    photos = [];
    disableVirtualScroll = false;

    constructor(private http: HttpClient) {}

    ngOnInit() {
        this.http.get<any[]>('https://jsonplaceholder.typicode.com/photos').subscribe(photos => {
            this.photos = photos;
        });
    }

    toggleVirtualScroll() {
        this.disableVirtualScroll = !this.disableVirtualScroll;
    }
}



